<template>
  <the-preloader
      v-if="isShow"
  >
  </the-preloader>
  <main class="main" v-cloak>
    <div class="container">
      <div class="search">
        <form
            class="search__form"
            @submit.prevent="getWeather"
        >
          <input
              type="text"
              class="search__form-input"
              placeholder="Укажите город"
              @input="getHint(city)"
              v-model="city"
          >

          <ul class="hints">
            <li
                v-for="hint in hints"
                :key="hint.data.city_kladr_id"
                class="hints__item"
            >
              <router-link class="hints__link" v-html="calcHint(hint.data.city)" :to="`/city/${hint.data.city}`">
              </router-link>
            </li>
          </ul>
        </form>

        <div class="search__hint center" v-if="!citiesData.length">
          Начните вводить город,
          например, <span>Ижевск</span>
        </div>
      </div>

      <div class="bookmarks center" v-if="!citiesData.length">
        <div class="bookmarks__text">
          Используйте значок «закладки»,
          чтобы закрепить город на главной
        </div>
        <img src="../assets/icons/bookmark.svg" class="bookmarks__image" alt="bookmark">
      </div>
      <div class="weather__cards" v-else>
        <the-card
            v-for="city in citiesData"
            :key="city.id"
            :city="city.name"
            :temperature="Math.round(city.main.temp)"
            :weather="city.weather[0].main.toLowerCase()"
            :path="city.path"
        ></the-card>
      </div>
    </div>
  </main>
</template>

<script>
// @ is an alias to /src
import { ref, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import thePreloader from '../components/thePreloader'
import theCard from '@/components/theCard'

export default {
  name: 'HomeView',
  components: {
    thePreloader,
    theCard
  },
  setup() {
    const apiKey = 'be7cfe8bd71367ae1b1667404762867e'
    const router = useRouter()
    const city = ref('')
    const cities = JSON.parse(localStorage.getItem('cities')) || [];
    const citiesData = ref([]);
    const hints = ref([]);
    const isShow = ref(true);

    async function getHint(query) {
      const url = 'https://suggestions.dadata.ru/suggestions/api/4_1/rs/suggest/address';
      const token = 'de9895a3eef9762c51e845bcc9c902428a1bcc4b';
      const options = {
        method: "POST",
        mode: "cors",
        headers: {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "bounds": "city",
          "Authorization": "Token " + token
        },
        body: JSON.stringify({
          query: query.toLowerCase(),
          locations: [{
            city_type_full: "город"
          }],
          locations_boost: [{
            kladr_id: "1800000100000"
          }],
          from_bound: {
            value: "city"
          },
          to_bound: {
            value: "city"
          },
          restrict_value: !1
        })
      }

      await fetch(url,  options)
        .then(res => res.json())
        .then(res => {
          hints.value = []
          res.suggestions.forEach(item => {
            if(item.data.city !== null) {
              hints.value.push(item);
            }
          });
        })
        .catch(e => console.log("error", e))
    }

    async function getWeather() {
      if(city.value !== '') {
        await fetch(`https://api.openweathermap.org/data/2.5/weather?q=${city.value}&appid=${apiKey}&lang=ru`)
            .then(res => res.json())
            .then(data => {
              if(data.cod !== '404') {
                router.push(`/city/${data.name}`)
              }
            })
      }
    }

    function getBulkWeather() {
      if(cities.length) {
        cities.forEach(async (city) => {
          await fetch(`https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${apiKey}&lang=ru&units=metric`)
            .then(res => res.json())
            .then(data => {
              data.path = `/city/${city}`;
              citiesData.value.push(data)
              console.log(data.path)
            });
        });
      }
    }
    function calcHint(item) {
      return `<span>${item.slice(0, city.value.length)}</span>${item.slice(city.value.length)}`
    }

    getBulkWeather();

    onMounted(() => {
      isShow.value = false;
    })

    return {
      city,
      getWeather,
      citiesData,
      getHint,
      hints,
      calcHint,
      isShow
    }
  }
}
</script>

